import { Component, OnInit, AfterViewInit, Input, Output, EventEmitter} from '@angular/core';
import { topAppBar, notchedOutline, textField, tabs } from 'material-components-web';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.pug',
  styleUrls: ['./app.component.styl']
})
export class AppComponent implements AfterViewInit {
  title = 'app';
  showOverlay: boolean = false
  overlayData: any = {}

  selectedLandIndex: number = -1

  ngAfterViewInit () {

  }

  mapUpdated() {
    console.log(this.overlayData)
    // this.showOverlay = true
  }

  openLand({index, selectedPolygon, selectedLand}) {
    console.log(selectedLand)
    this.showOverlay = true
    this.selectedLandIndex = index
    this.overlayData = selectedLand
  }

  onNewNote(note) {
    console.log(this.selectedLandIndex, note)
  }
}



