import { Component, OnInit, AfterViewInit, Input, Output, EventEmitter} from '@angular/core';
import { topAppBar, notchedOutline, textField, tabs } from 'material-components-web';
import dt from "./downloadtext";

// https://developers.google.com/maps/documentation/javascript/shapes#polygons
// https://developers.google.com/maps/documentation/javascript/examples/layer-data-polygon
// http://mathworld.wolfram.com/PolygonArea.html
// https://developers.google.com/maps/documentation/javascript/geometry#Distance

function addLand(data: Array<any>, onPolygonClickCallback) {

  let map = window['map']
  let colors = ['red', 'green', 'blue', 'yellow', 'orange']
// window.google.maps.geometry.spherical.computeArea(paths[0])

  // data = data
  // .map((land) => {
  //   land.area = window['google'].maps.geometry.spherical.computeArea(land.path)
  //   return land
  // })


  data
  // // add big ones first
  // .sort((a:any , b:any ) => {
  //   return b.area - a.area
  // })
  .forEach((land, index) => {
    let path = land.path
    var polygon = new window['google'].maps.Polygon({
      paths: path,
      strokeColor: colors[parseInt(Math.random()*10 +'')%5], //'#FF0000',
      strokeOpacity: 0.8,
      strokeWeight: 2,
      fillColor: '#FF0000',
      fillOpacity: 0.35
    })
    // window['poly'].push(polygon)
    polygon.setMap(map)

    polygon.addListener('click', function() {
      map.setCenter(path[0]);
      onPolygonClickCallback(index, polygon, land)
    })
  })

  map.setCenter(data[0].path[0])
}

// var onPolygonClickCallback = (selectedPolygon, selectedLand) => {} //TODO NIY

window['initMap'] = () => {
  console.log('initMap')
  var map = new window['google'].maps.Map(document.getElementById('map'), {
    zoom: 18,
    mapTypeId: 'terrain'
  });
  window['map'] = map
}

@Component({
  selector: 'app-import-tool',
  templateUrl: './import-tool.component.pug',
  styleUrls: ['./import-tool.component.styl']
})
export class ImportToolComponent implements OnInit, AfterViewInit{

  @Output("select") onAreaSelect: EventEmitter<any> = new EventEmitter<any>();

  constructor() { }

  ngOnInit() {

  }

  ngAfterViewInit () {
    console.log('ngAfterViewInit')
    if (window['gMapReady']) {
      console.log('gMapReady', window['gMapReady'])
      window['initMap']()
    }
  }

  importFile() {
    let _this = this
    document.getElementById("import-file").click()
    document.getElementById("import-file").addEventListener('change', (e) => {

      let files = document.getElementById("import-file")['files']
      console.log()

      if (files && files[0]) {
          var myFile = files[0];
          var reader = new FileReader();

          reader.addEventListener('load', function (e: any) {
            // output.textContent = e.target.result;
            console.log(e.target.result)
            try {
              addLand(JSON.parse(e.target.result), (index, selectedPolygon, selectedLand) => {
                _this.onAreaSelect.emit({index, selectedPolygon, selectedLand})
              })
              localStorage.setItem("fui.yoga", e.target.result)
            } catch(e) {
              console.log(e)
            }
          });

          reader.readAsText(myFile);
          // reader.readAsBinaryString(myFile);
      }

    })

  }

  downloadFile() {
    dt.downloadText(localStorage.getItem("fui.yoga"))
  }

}



