import { Component, OnInit, AfterViewInit, Input, Output, EventEmitter} from '@angular/core';

import { topAppBar, notchedOutline, textField, slider } from 'material-components-web';


@Component({
  selector: 'app-overlay',
  templateUrl: './overlay.component.pug',
  styleUrls: ['./overlay.component.styl']
})
export class OverlayComponent implements OnInit, AfterViewInit {

  @Input("data") data: any = {}
  @Output("close") close: EventEmitter<any> = new EventEmitter<any>();
  @Output("onNewNote") onNewNote: EventEmitter<any> = new EventEmitter<any>();


  elId: string = `card-${parseInt((Math.random() * 100).toString())}`
  labelId: string = `${this.elId}-label`

  notes = ''

  constructor() { }

  ngOnInit() {
  }

  ngAfterViewInit() {
    const { MDCTextField } = textField
    const {MDCNotchedOutline} = notchedOutline

    let textField_videoOverlay = new MDCTextField(document.querySelector(`#${this.elId} .mdc-text-field`));
    let notchedOutline_videoOverlay = new MDCNotchedOutline(document.querySelector(`#${this.elId} .mdc-text-field .mdc-notched-outline`));
  }

}



